.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
    text-align: center;
    text-shadow: 0 0 0.03em;
}

.h2,
.h3,
.h4,
.h5,
.h6,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 2*$headings-margin-bottom;
}

h2 + h3,
h3 + h4 {
    margin-top: 2.5*$headings-margin-bottom;
}

h1, .h1 { @include media-breakpoint-down(sm){ font-size: 0.7 * $h1-font-size; } }
h2, .h2 {
    @include media-breakpoint-down(sm){ font-size: 0.8 * $h2-font-size; }
    @include media-breakpoint-down(xs){ font-size: 0.7 * $h2-font-size; }
}
h3, .h3 {
    @include media-breakpoint-down(sm){ font-size: 0.8 * $h3-font-size; }
    @include media-breakpoint-down(xs){ font-size: 0.7 * $h3-font-size; }
}
h4, .h4 { @include media-breakpoint-down(xs){ font-size: 0.8 * $h4-font-size; } }
h5, .h5 { @include media-breakpoint-down(xs){ font-size: 0.8 * $h5-font-size; } }
h6, .h6 { @include media-breakpoint-down(xs){ font-size: 0.8 * $h6-font-size; } }

.lead {
    font-family: $font-family-monospace;
    text-align: center;
}

.text-sans-serif {
    font-family: $font-family-sans-serif !important;
}